import { createSelector } from 'reselect';
import { SliceName } from '../../const';
export const getPlayers = (state) => state[SliceName.PLAYERS].players;
export const getPlayersLoadedStatus = (state) => state[SliceName.PLAYERS].isPlayersLoaded;
export const getLocalPlayers = (state) => state[SliceName.PLAYERS].localPlayers;
export const getLocalPlayersLoadedStatus = (state) => state[SliceName.PLAYERS].isLocalPlayersLoaded;
export const getAllPlayers = createSelector([getPlayers, getLocalPlayers], (players, localPlayers) => [...players, ...localPlayers]);
