export const adaptNewsToClient = (serverNews) => ({
    id: serverNews.id,
    image: serverNews.image,
    thumbImage: serverNews.thumb_image,
    date: serverNews.date,
    title: serverNews.title,
    content: serverNews.content,
    slug: serverNews.slug,
});
export const adaptNewsArrayToClient = (serverNews) => serverNews.map((news) => adaptNewsToClient(news));
