import { SliceName } from '../../const';
export const getLastTournaments = (state) => state[SliceName.TOURNAMENTS].lastTournaments;
export const getLastTournamentsLoadedStatus = (state) => state[SliceName.TOURNAMENTS].isLastTournamentsLoaded;
export const getPaginatedTournaments = (state) => state[SliceName.TOURNAMENTS].paginatedTournaments;
export const getTournamentPagesCount = (state) => state[SliceName.TOURNAMENTS].pagesCount;
export const getUpcomingTournaments = (state) => state[SliceName.TOURNAMENTS].upcomingTournaments;
export const getUpcomingTournamentsLoadedStatus = (state) => state[SliceName.TOURNAMENTS].isUpcomingTournamentsLoaded;
