export const adaptArticleToClient = (serverArticle) => ({
    id: serverArticle.id,
    image: serverArticle.image,
    thumbImage: serverArticle.thumb_image,
    date: serverArticle.date,
    title: serverArticle.title,
    content: serverArticle.content,
    slug: serverArticle.slug,
});
export const adaptArticlesToClient = (serverArticles) => serverArticles.map((serverArticle) => adaptArticleToClient(serverArticle));
