import { createAsyncThunk } from '@reduxjs/toolkit';
import { ApiRoute } from '../../const';
export const fetchPlayers = createAsyncThunk('players/fetchPlayers', async (_arg, { extra: api }) => {
    const { data } = await api.get(ApiRoute.PLAYERS);
    return data;
});
export const fetchLocalPlayers = createAsyncThunk('players/fetchLocalPlayers', async (_arg, { extra: api }) => {
    const { data } = await api.get(`${ApiRoute.PLAYERS}?category=local`);
    return data;
});
